// Кнопки секции HEADER, MOBILEMENU

  .header__btn,
  .menu__btn {
    height: 44px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
  }

  .header__btn {
    width: 160px;
  }

  .header__btn-icon {
    width: 4px;
    height: 8px;
    fill: currentColor;
  }

  .menu__btn {
    width: 209px;

    @include tab {
      width: 186px;
    }
  }

  .menu__btn-icon {
    margin-left: 16px;

    @include tab {
      margin-left: 14px;
    }

    fill: currentColor;
  }

.form__btn {
  
    &--dark {
      color: var(--btn-color-light);
      background-color: var(--btn-color-dark);
    
      &:hover,
      &:focus {
        background-color: var(--btn-color-dark-accent);
      }
    }
  
   &--light {
      color: var(--btn-color-dark);
      background-color: var(--btn-color-light);
    
      &:hover,
      &:focus {
      background-color: var(--btn-color-light-accent);
      }
   }
  
   display: block;
   margin-top: 30px;
   margin-left: auto;
   margin-right: auto;
   font-size: 16px;
   line-height: 1.31;
   font-weight: 700;
   height: 44px;
   min-width: 130px;
   transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
   transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

